@import '../../global.scss';

@include desktop {
    .signup-right-container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 80%;
        color: black;

        h1 > span {
            color: blue;
            text-decoration: underline;
            cursor: pointer;

        }

        input[type="text"] {
            &::placeholder {
                padding-left: 25px;
            }
        }

        input[type="date"] {
            padding-left: 25px;
  
        }

        h1 {
            padding-bottom: 10px;
        }

        input {
            height: 50px;
            border-radius: 10px;
            margin: 10px 10px;
            border: none;
            background-color: white;
            font-size: larger;
            padding-left: 5px;


        }

        form {
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            
            width: 90%;

            .top {
                display: flex;
                width: 100%;

                input {

                    height: 50px;
                    width: 100%;

                }
            }

            .center {
                display: flex;
                width: 100%;
                flex-direction: column;


            }

            .bottom {
                display: flex;
                flex-direction: column;

                width: 100%;

                span {
                    margin-left: 15px;
                }

                input {
                    width: 95%;

                }

                button {
                    border-radius: 10px;
                    font-weight: 700;
                    font-size: 23px;
                    width: 60%;
                    color: white;
                    height: 50px;
                    background-color: rgb(255, 0, 0);
                    text-transform: none;
                    align-self: center;

                    margin: 15px 0;
                    cursor: pointer;
                }

                


            }




        }
    }
}