@import '../../global.scss';

.main-container {

    @include mobile {

        height: 100%;
        width: 100vw;
        display: flex;
        align-items: center;
        justify-content: center;


        .left {
            display: none;
        }

        .right {

            display: flex;
            flex-direction: column;
            width: 100%;
            height: 80%;

            .login-right-container {
                width: 100%;
                height: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: space-around;

                .bottom {
                    width: 90%;
                    display: flex;
                    flex-direction: column;

                    span {

                        align-self: center;
                        font-weight: bold;
                        color: #655555;
                        

                    }



                    form {
                        display: flex;
                        flex-direction: column;

                        button {
                            border-radius: 10px;
                            margin: 20px 0;
                            height: 60px;
                            width: 80%;
                            align-self: center;
                            background-color: #FF0000;
                            font-size: 25px;




                        }

                        span {
                            font-weight: bold;
                            text-align: end;
                            margin-right: 5px;
                            align-self: flex-end;

                            a {
                                text-decoration: none;
                                color: #AE8A8A;
                            }
                        }
                    }

                    .socials {
                        display: flex;
                        align-items: center;
                        justify-content: center;

                        .icon-google, .icon-facebook, .icon-twitter {
                            background-color: white;
                            border-radius: 15px;
                            margin: 15px 10px;
                            width: 70px;
                            height: 70px;

                            display: flex;
                            align-items: center;
                            justify-content: center;

                            img {
                                width: 100%;
                            }


                        }



                    }
                }

                input {
                    width: 100%;
                    height: 60px;
                    margin: 10px 0;
                    border-radius: 10px;
                    border: none;

                    &::placeholder {
                        padding-left: 15px;
                        font-size: 20px;
                        color: #AE8A8A;
                    }
                }

                h1 {
                    font-size: 60px;
                }



                p {
                    order: 5;
                }

 


            }


        }
    }

    @include desktop {

        width: 60%;
        height: 80%;
        background-color: #FDE1FA;
        color: white;
        border-radius: 30px;
        opacity: 0.8;
        display: flex;

        .left {
            width: 50%;
            height: 100%;
            //border: 1px solid black;
            display: flex;
            align-items: center;
            justify-content: center;

            .img-container {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 90%;
                height: 90%;
                //border: 1px solid black;
                border-radius: 30px;
                background-color: white;

                img {
                    width: 100%;
                }

            }
        }

        .right {
            width: 50%;
            right: 100%;
            //border: 1px solid black;
            display: flex;
            align-items: center;
            justify-content: center;
        }




    }


}