@import '../../../global.scss';

@include desktop {
    .input {
        position: relative;
        width: 95%;
        
        input {
            width: 100%;



            &::placeholder {
                padding-left: 15px;
                font-size: 20px;
                color: #AE8A8A;
            }

        }
    
        svg {
            
            position: absolute;
            right: 10px;
            top: 35px;
            cursor: pointer;


        }
    }
}


@include mobile {

    input {
        padding-left: 15px;
        font-size: larger;
        color: black;


        &::placeholder {
            padding-left: 15px;
            font-size: 20px;
            color: #AE8A8A;
        }
    }
}
