@import '../../global.scss';

@include desktop {


    .login-right-container {
        width: 100%;
        height: 90%;
        //border: 1px solid black;
        border-radius: 30px;
        color: black;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;

        p > span {
            color: blue;
            text-decoration: underline;
            cursor: pointer;

        }

        a::visited {
            color: blue;
        }

        p {
            align-self: end;
            justify-self: flex-start;
            font-size: 13px;
            text-align: end;
            font-weight: bold;
            margin-right: 50px;
        }

        .top {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            flex: 2;
            height: 50%;
            width: 100%;
            //border: 1px solid black;
            font-size: 1.5em;


        }

        .bottom {
            flex: 5;
            width: 90%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;

            form {
                display: flex;
                position: relative;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                //border: 1px solid black;
                width: 90%;

                span {
                    align-self: flex-end;

                    a {
                        margin-right: 10px;
                    }

                }

                input {
                    border-radius: 10px;
                    font-size: larger;
                    width: 100%;
                    height: 50px;
                    background-color: white;
                    margin: 15px 0;
                    padding-left: 15px;
                    border: none;

                    &::placeholder {
                        padding-left: 15px;

                    }
                    
                }

                .input {
                    
                    svg {
                        top: 28px;
                        right: 15px
                    }
                }


                button {
                    border-radius: 10px;
                    font-weight: 700;
                    font-size: 23px;
                    width: 100%;
                    color: white;
                    height: 50px;
                    background-color: rgb(255, 0, 0);
                    text-transform: none;

                    margin: 15px 0;
                    cursor: pointer;
                }



                .eye-1 {
                    position: absolute;
                    right: 15px;
                    top: 10px;

                }

                .eye-2 {

                    position: absolute;
                    right: 15px;
                    top: 93px;
                    cursor: pointer;


                }
            }



            .socials {
                display: flex;
                margin-top: 15px;

                align-items: center;
                justify-content: center;

                .icon-google,
                .icon-facebook,
                .icon-twitter {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    background-color: white;
                    margin: 0 5px;
                    border-radius: 10px;
                    cursor: pointer;


                }
            }

        }

    }


}